    background-color:#2c324e;
    border:10px solid transparent;
    border-radius:10px;
    width:100%;
    overflow-x: auto;
    overflow-y: hidden;
    margin:2em auto;
}
.topSearch::-webkit-scrollbar
{
    width: 10px;
    background-color: transparent;
}

.topSearch::-webkit-scrollbar-thumb
{
    background-color: #513858;
}
.topSearch__coinContainer{
    display:flex;
}
.topSearch__coin{
    display:flex;
    justify-content: space-between;
    background-color: #41496c;
    margin:1em;
    grid-gap:1em;
    gap:1em;
    padding:.75em 1em;    
    border-radius: 15px;
    transition:all 1s;
}

.topSearch__coin:hover{
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
    transition:all 1s;
}
.topSearch__coin div{
    display:flex;
    width:250px;
    grid-gap:1em;
    gap:1em;
    align-items: center;
}
.topSearch__coin__coinName{
    color:white;
    text-align: left;
    font-size:1.25rem;
    font-weight:600;
}
.topSearch__coin__coinRank{
    width:30%;
    text-align: center;
    font-size:1.15rem;
    font-weight:500;
    padding:.75em .5em;
    border-radius:15px;
    background-color:black;
    color:#4fcc84;
}
@media only screen and (min-width: 768px) {
    .coinInfo{
        padding:1em 2.5em;
    }
}
@media only screen and (min-width: 1024px) {
    .coinInfo__top__right{
        width:85%;
        padding:1.5em 1.25em 1.25em;
        display:flex;
        flex-wrap: wrap;
        align-items: center;
        justify-content: space-around;
    }
    .coinInfo__topRight__leftBottom,.coinInfo__topRight__rightBottom{
        height: 50%;
        width:40%;
        font-size:1.25rem;
        font-weight: 600;
        color:white;
        text-align: center;
        margin-top: 1em;
        border-radius: 10px;
        padding:1.5em .25em;    
        transition:-webkit-transform .5s;    
        transition:transform .5s;    
        transition:transform .5s, -webkit-transform .5s;
    }
    .coinInfo__topRight__leftBottom{
        background-color:#cd8b24;
    }
    .coinInfo__topRight__rightBottom{
        background-color: #0484ad;
    }
    .coinInfo__topRight__leftBottom:hover{
        background-color:#edae4b;
        -webkit-transform: scale(1.05);
                transform: scale(1.05);
        transition:-webkit-transform .5s;
        transition:transform .5s;
        transition:transform .5s, -webkit-transform .5s;
    }
    .coinInfo__topRight__rightBottom:hover{
        background-color:#21a4ce;
        -webkit-transform: scale(1.05);
                transform: scale(1.05);
        transition:-webkit-transform .5s;
        transition:transform .5s;
        transition:transform .5s, -webkit-transform .5s;
    }
    .fa-bitcoin,.fa-globe-asia{
        font-size:4rem;
        margin-top:.25em;
        display: block;
    }
    .exchange{
        margin-bottom:1em;
    }
    .exchange__form{
        text-align: center;
    }
    .coinInfo__top__right a{
        display:inline;
        display:initial;
    }
    .exchange__form label{
        background-color:#d0435e;
        font-weight:600;
        border-top-left-radius: 10px;
        border-bottom-left-radius: 10px;
        padding:.125em .5em;
    }
    .coinInfo{
        padding:1em 1.5em;
    }
    .coinInfo__top{
        display:flex;
        font-size:1.25rem;
    }
    .coinInfo__top__left{
        width:65%;
        padding:0 1em;
    }
    .coinInfo__bot__graphSide{
        width:65%;
        margin:2em auto;
        padding:1em 1.5em;
    }
    .daySelect{
        width:60%;
        background:#0d5379;
        padding:.05em 0;
    }
}
@media only screen and (min-width: 1100px) {
    .coinInfo__top__right{
        width:35%;
    }
    .coinInfo__bot{
        display:flex;
        align-items: center;
    }
    .coinInfo__container{
        margin-top:2em;
    }
    .coinInfo__infoSide{
        font-size: 1.25rem;
    }
    .coinInfo__bot{
        padding:2em 1.5em 1em 2em;
        justify-content: center;
    }
    .topSearch__coinContainer{
        display:inline;
        display:initial;
    }
    .topSearch{
        width: 85%;
        height:-webkit-fit-content;
        height:-moz-fit-content;
        height:fit-content;
        padding:1.5em 1em;
        border:none;
        max-height:700px;
        overflow: auto;
    }
    .coinInfo__topRight__leftBottom,.coinInfo__topRight__rightBottom{
        font-size:1.45rem;
    }
}
@media only screen and (min-width: 1440px){
    .coinInfo__top{
        font-size:1.5vw;
    }
    .coinInfo__container{
        font-size:1.5vw;
        margin-top:.75em;
    }
}
.lost{
    position:relative;
    /* Prevent margin collabse */
    padding-top: 1px;
    margin-top: -1px;
    /* *********************** */
}
.lost__header{
    position:relative;
    color:#f0a234;
    font-size:7rem;
    z-index:3;
    margin:1em auto 0;
    height:-webkit-fit-content;
    height:-moz-fit-content;
    height:fit-content;
    width:-webkit-fit-content;
    width:-moz-fit-content;
    width:fit-content;
}
.lost__p{
    color:white;
    font-size:2.5rem;
    text-align: center;
    width:80%;
    margin:auto;
    font-weight: 600;
    position:relative;
    z-index:3;
}
.lost__img{
    position:absolute;
    top:5%;
    width:100%;
    opacity: .3;
    z-index:1;
}
.lost__link{ 
    background: linear-gradient(to right, #db36a4, #f7ff00); 
    font-size:1.25rem;
    font-weight: 600;
    color:white;
    text-align: center;
    margin:1em auto;
    padding:.5em 2em;
    width:-webkit-fit-content;
    width:-moz-fit-content;
    width:fit-content;
    border-radius: 15px;
    position:relative;
    z-index:3;
    cursor:pointer;
}
@media only screen and (min-width: 1024px) {
    .lost__header{
        font-size:10rem;
    }
    .lost__img{
       top:30%;
    }

}
